  AddDocument(body, appDocumentID) : Observable<any> {
    this.auth.getToken().then(resp => { this.token = resp });
    if (appDocumentID == 0) {
      const url = this.api + 'Application/AddDocument ';      
      return this.http.post<any>(url, body, {reportProgress: true, observe: 'events', headers: new HttpHeaders({'Authorization': 'bearer ' + this.token })}).pipe();    
    }
    else {
      const url = this.api + 'Application/UpdateDocument';
      return this.http.put<any>(url, body, {observe: 'events', headers: new HttpHeaders({'Authorization': 'bearer ' + this.token })}).pipe();  
    }
  }

  public sendFormData(formData) {
    const url = this.api + 'Application/AddDocument ';
    return this.http.post<any>(url, formData, {
      reportProgress: true,
      observe: 'events'
    });
}

